.pz-daoru-modal .ant-modal-header {
  padding: 0;
}

.pz-daoru-modal .ant-modal-body {
  min-height: 300px;
}

.pz-daoru-tabs .ant-tabs-nav {
  margin-bottom: 0;
}

.pz-daoru-tabs .ant-tabs-nav-wrap {
  padding-left: 24px;
  padding-top:8px;
}

.pz-daoru-input-wrap {
  padding: 16px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pz-daoru-input-wrap.no-padding {
  padding: 0;
}

.pz-daoru-input-wrap-input {
  width: 35%;
}

.pz-daoru-success-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pz-daoru-text-wrap {
  height: 100px;
  font-size: 16px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.pz-daoru-success-info {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border:1px solid #d9d9d9;
  border-radius: 8px;
  padding: 12px;
}

.pz-daoru-success-tip{
  margin-bottom: 12px;
}

.pz-daoru-success-title {
  flex: 1;
  font-weight: 700;
  font-size: 16px;
  color: var(--ant-primary-color);
  max-width: 134px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pz-daoru-success-user {
  display: flex;
  align-items: center;
  font-size: 14px;
  width: 140px;
}

.pz-daoru-success-avatar {
  display: block;
  width: 40px;
  overflow: hidden;
  border-radius: 50%;
  height: 40px;
  margin-right: 8px;
}

.pz-daoru-text-error {
  color: #F34242
}

.pz-daoru-help-img {
  width: 100%;
}